export class NasaRoverImageViewModel {
    constructor({ id, sol, earth_date: earthDate, img_src: imageSrc, camera: { id: cameraId, full_name: cameraName }, rover: { id: roverId, name: roverName, status }, }) {
        this.liked = false;
        this.id = id;
        this.imageSrc = imageSrc;
        this.imageAlt = `Rover image #${id}`;
        this.title = roverName;
        this.subtitle = cameraName;
        this.date = `${earthDate} / sol ${sol}`;
    }
}
